<script>
import { mapActions } from 'vuex'
import uniqid from 'uniqid';

import Categories from '../components/sidebar/Categories.vue';
import BaseContextMenu from '../components/BaseContextMenu.vue';

export default {
    name: 'Sidebar',
    props: {
        languages: Array,
        tags: Array,
        isHidden: Boolean,
    },
    components: {
        Categories,
        BaseContextMenu
    },
    data: () => ({
        showContextMenu: false,
        contextMenuTop: 0,
        contextMenuLeft: 0,
        contextMenuItems: [{
            name: 'rename category',
            cb: 'editLanguageName'
        }, {
            name: 'delete category',
            cb: 'removeLanguage'
        }],
    }),
    methods: {
        ...mapActions([
            'addLanguage',
            'addSnippet',
            'editLanguageName',
            'finishEditLanguageName',
            'removeLanguage',
            'selectLanguage',
            'selectTag',
            'updateLanguageName',
        ]),
        handleAddLanguage(name) {
            let id = uniqid();
            let language = {
                id,
                name,
                isSelected: false,
                snippets: [],
                isInEditMode: false
            }
            this.addLanguage(language);
            this.selectLanguage(id);
            this.addSnippet(uniqid())
        },
        handleUpdateLanguage({ id = null, name = null }) {
            this.updateLanguageName({ id, name });
        },
        handleShowContextMenu({ x = event.x, y = event.y, id }) {
            this.selectLanguage(id);
            this.contextMenuTop = y;
            this.contextMenuLeft = x;

            if (this.showContextMenu) {
                this.showContextMenu = false;
                setTimeout(() => {
                    this.showContextMenu = true;
                }, 10);
            } else {
                this.showContextMenu = true;
            }
        },
        handleFinishEditMode() {
            this.finishEditLanguageName();
        },
        handleBaseContextClick(cb) {
            cb && this[cb]()
        },
        openModal() {
            this.$emit('openModal', 'settings');
        }
    }
};

</script>
